<script>
import { getMember } from "@/service/getData";
export default {
  data() {
    return {
      usernameS: "",
      user: {},
      chartdata: [
        {
          name: "總資產",
          data: [
            {
              label: "2020",
              value: 0,
            },
            {
              label: "2021",
              value: 0,
            },
            {
              label: "2022",
              value: 0,
            },
            {
              label: "2023",
              value: 0,
            },
          ],
        },
      ],
    };
  },
  methods: {
    getMember() {
      //獲取會員訊息
      getMember()
        .then((res) => {
          if (res.code == 1) {
            console.log(res.data);
            this.user = res.data;
            this.usernameS = this.user.name.slice(0, 1);
          } else if (res.code == -1) {
            this.$Message.error(this.loginmsg);
            this.$store.commit("setMember", null);
            this.$store.commit("setToken", null);
            this.$router.push("/");
          }
        })
        .catch((res) => {
          this.$Message.error("請重新登入!");
          this.$store.commit("setMember", null);
          this.$store.commit("setToken", null);
          this.$router.push("/");
        });
    },
  },
  created() {
    this.getMember();
  },
};
</script>

